.p-sub-contact__inner.l-inner {
  max-width: 700px + strip-unit($padding-pc) * 2;
}

.p-sub-contact__text {
  @include mq(sp) {
    font-size: rem(14);
  }
}

.p-sub-contact__contents {
  margin-top: rem(41);
}

.p-sub-contact__content + .p-sub-contact__content {
  margin-top: rem(20);
}

.p-sub-contact__content.p-sub-contact__content--radio {
  margin-top: rem(24);
}

.p-sub-contact__content.p-sub-contact__content--textarea {
  margin-top: rem(17);
}
.p-sub-contact__content.p-sub-contact__content--check {
  margin-top: rem(30);
  text-align: center;
  @include mq(sp) {
    text-align: left;
  }
}

.p-sub-contact__content.p-sub-contact__content--button {
  text-align: center;
  margin-top: rem(48);
}

.p-sub-contact__head {
  @include mq(sp) {
    font-size: rem(14);
  }
}

.p-sub-contact__title {
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.04em;
  padding-bottom: rem(12);
  @include mq(sp) {
    font-size: rem(14);
  }
}

.p-sub-contact__bold {
  font-weight: $font-weight-bold;
  @include mq(tab) {
    display: block;
    margin-top: rem(8);
    font-size: rem(14);
  }
}

.p-sub-contact__privacy {
  display: inline-block;
  color: $ac-color;
  text-decoration: underline;
}

.p-sub-contact__item.p-contact-check {
  margin-top: rem(33);
}